/*------------------------------------*\
    $FORMS
\*------------------------------------*/

input,
select,
textarea,
button {
    font-family: inherit;
}

input,
select,
textarea {
    font-size: 16px;
    line-height: 3em;
    min-height: 3em;
    color: $color-default;
    border: 1px solid $color-light;
    padding: 0 1em;
    width: 100%;
}


/* TEXTAREA */

textarea {
    vertical-align: top;
    min-height: 200px;
    line-height: 1.5em;
    padding: 1em;
}


/* SUBMIT */

input[type="submit"],
button {
    cursor: pointer;
    transition: $transition;
    border: none;
}

@include tablet {
    input,
    select,
    textarea,
    button {
        -webkit-appearance: none;
        border-radius: 0;
    }
}


/*------------------------------------*\
    $GRAVITY FORMS
\*------------------------------------*/

.gfield_required {
    display: inline-block;
    margin-left: 4px;
}

.gform_fields {
    @include clearfix;
    margin-left: -12px;
    margin-bottom: 0;
}

.gfield {
    float: left;
    width: 100%;
    padding-left: 12px;
    padding-bottom: 12px;
}

.gf_left_half,
.gf_right_half,
.gf_left_third,
.gf_middle_third,
.gf_right_third,
.gf_first_quarter,
.gf_second_quarter,
.gf_third_quarter,
.gf_fourth_quarter {
    float: left;
}

.gf_left_half,
.gf_right_half {
    width: 50%;
}

.gf_right_half {
    clear: right;
}

.gf_right_half+.gfield:not(.gf_left_half) {
    clear: both;
}

.gf_left_third,
.gf_middle_third,
.gf_right_third {
    width: 33.333%;
}

.gf_first_quarter,
.gf_second_quarter,
.gf_third_quarter,
.gf_fourth_quarter {
    width: 25%;
}

@include phone {
    .gf_left_half,
    .gf_right_half,
    .gf_left_third,
    .gf_middle_third,
    .gf_right_third,
    .gf_first_quarter,
    .gf_second_quarter,
    .gf_third_quarter,
    .gf_fourth_quarter {
        float: none;
        width: 100%;
    }
}


/* HIDDEN ELEMENTS */

.hidden_label .gfield_label,
.gform_validation_container,
.gform_hidden,
.gfield_visibility_hidden,
.screen-reader-text,
.gform_wrapper label.hidden_sub_label {
    @include visually-hidden;
}


/* FOOTER */

.gform_footer {
    padding-top: 12px;
}


/* SPINNER */

.gform_ajax_spinner {
    margin-left: 20px;
    border: 4px solid rgba($color-primary, 0.3);
    border-left: 4px solid $color-primary;
    animation: spinner 1.1s infinite linear;
    border-radius: 50%;
    width: 30px;
    height: 30px;
}

@keyframes spinner {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.gfield_label {
    display: block;
    margin-bottom: 1em;
}


/*------------------------------------*\
    $CUSTOM STYLED FIELDS
\*------------------------------------*/


/* Checkbox and Radio Buttons */

.gfield_checkbox,
.gfield_radio {
    input[type="checkbox"],
    input[type="radio"] {
        @include visually-hidden;
    }
    label {
        position: relative;
        padding-left: 2em;
    }
    li+li {
        margin-top: 0.5em;
    }
    .custom-input {
        position: absolute;
        top: 0;
        left: 0;
        width: 20px;
        height: 20px;
        cursor: pointer;
        border: 1px solid $color-primary;
    }
    /* SELECTED */
    input[type="checkbox"]:checked+label span.custom-input,
    input[type="radio"]:checked+label span.custom-input {
        background-color: $color-primary;
    }
}


/* CHECKBOX */

.gfield_checkbox input[type="checkbox"]:checked+label span.custom-input:before {
    content: '\f00c';
    position: absolute;
    top: 0;
    left: 2px;
    font-family: $font-icon;
    font-weight: 700;
    color: $color-white;
    font-size: 14px;
}


/* RADIO BUTTON */

.gfield_radio input[type="radio"]+label span.custom-input {
    border-radius: 100px;
}

.gfield_radio input[type="radio"]:checked+label span.custom-input:before {
    content: '';
    width: 10px;
    height: 10px;
    border-radius: 100px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -5px;
    margin-top: -5px;
    background: $color-white;
}


/* Datepicker */

.custom-datepicker {
    position: relative;
    &:before,
    &:after {
        position: absolute;
        z-index: 1;
        width: 34px;
        right: 0;
    }
    &:before {
        content: '';
        top: 0;
        right: 0;
        bottom: 0;
        background-color: $color-primary;
    }
    &:after {
        content: '\f133';
        top: 50%;
        transform: translateY(-50%);
        font-family: $font-icon;
        color: $color-white;
        text-align: center;
    }
    input {
        position: relative;
        z-index: 5;
        background-color: transparent;
    }
}


/*------------------------------------*\
    $ERRORS
\*------------------------------------*/

.validation_error {
    display: block;
    margin-bottom: 1em;
    color: $color-error;
    &:before {
        content: '\f06a';
        font-family: $font-icon;
        margin-right: 0.5em;
    }
}

.gfield_error {
    input,
    select,
    textarea {
        border: 1px solid $color-error;
    }
    .validation_message {
        color: $color-error;
        margin-top: 0.5em;
    }
}


/*------------------------------------*\
    $CONFIRMATION MESSAGE
\*------------------------------------*/

.gform_confirmation_message {
    display: block;
}


/*------------------------------------*\
    $PLACEHOLDER TEXT
\*------------------------------------*/

@include placeholder {
    color: $color-default;
    opacity: 1;
}
