/*------------------------------------*\
    $ACCORDION
\*------------------------------------*/

.accordion__item {
	margin-bottom: 24px;
}

.accordion__item__heading {
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: relative;
	cursor: pointer;
	padding: 1em;
	color: $color-white;
	background-color: $color-primary;
	margin-bottom: 0;
}

.accordion__item__content {
	padding: 1em;
	border: solid $color-primary;
	border-width: 0 1px 1px 1px;
}




